.container{
  text-align: center;
}

.topContainer{
  min-height: 100px;
}

.centerContainer{
  min-height: 200px;
  position: relative;
}

.btn{
  display: inline-block;
  background: #db1d29;
  color: #FFF;
  width: 200px;
  height: 200px;
  line-height: 120px;
  border-radius: 50%;
  border-color: transparent;
  text-align: center;
  font-weight: bold;
  font-size: 4em;
  vertical-align: middle;
  overflow: hidden;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.29);
  border-bottom: solid 2px #bd6565;
  transition: .05s;
}

.btn:active{
  -ms-transform: translateY(4px);
  -webkit-transform: translateY(4px);
  transform: translateY(4px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}

.btn:focus{
  outline: none;
}

.restartbtn{
  display: inline-block;
  background: black;
  color: #FFF;
  width: 280px;
  height: 180px;
  line-height: 120px;
  border-radius: 10%;
  border-color: transparent;
  text-align: center;
  font-weight: bold;
  font-size: 3em;
  vertical-align: middle;
  overflow: hidden;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.29);
  transition: .05s;
}

.restartbtn:active{
  -ms-transform: translateY(4px);
  -webkit-transform: translateY(4px);
  transform: translateY(4px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}